import { loadData } from '@/api/common'
import Scroll from '@/components/scroll'
export default {
  components: {
    Scroll
  },
  data() {
    return {
      isShow: true,
      isDelete: false,
      listInfoDate: {}
    }
  },
  created() {
    this.listDate()
  },
  methods: {
    toAddressEdit(id) {
      this.$router.push(`/memberCenter/components/addressEdit/${id}`)
    },
    listDate() {
      loadData('/member/address/list').then(data => {
        if (data.data.length === 0) {
          this.isShow = false
        } else {
          this.isShow = true
          this.listInfoDate = data.data
        }
      })
    },
    deleteBtn(id) {
      loadData('/member/address/delete/' + id).then(data => {
        this.isDelete = true
      })
    },
    affirm() {
      this.isDelete = false
      this.listDate()
    }
  }
}



